import { Edit } from "@mui/icons-material";
import { Box, IconButton, Rating } from "@mui/material";
import { Fragment, useState } from "react";
import CreateProveedorModal from "./create-proveedor";
const ProveedorColumns = [
    {
        Header: "Codigo",
        accessor: "codigoProveedor"
    },
    {
        Header: "Nombre",
        accessor: "nombreProveedor"
    },
    {
        Header: "Dirrecion",
        accessor: "dirrecion"
    },
    {
        Header: "Telefono",
        accessor: "telefono"
    },
    {
        Header: "Credito",
        accessor: "credito"
    },
    {
        Header: "Acciones",
        accessor: "acciones",
        Cell: ({
            row
        }) => {
            const [openModal, setOpenModal] = useState(false);
            return <Fragment>
                <IconButton onClick={() => setOpenModal(true)}>
                    <Edit sx={{
                        fontSize: 18,
                        color: "text.disabled"
                    }} />
                </IconButton>
                <CreateProveedorModal editProduct open={openModal}  data={row.original} onClose={() => setOpenModal(false)} />
            </Fragment>;
        }
    }];
export default ProveedorColumns;